import Ratings from './stars';

function Content(){
    return(
        <div className="content-section">
            <div className="row">
                <div className="col-md-8">
                    <div className="container">
                    <div className="intro">
                        <h1>INTRODUCING</h1>
                        <p>A complete platform for online and offline tutoring which is a 
                        network of knowledge where student can learn from the desired teacher 
                        as well as it enables teacher to teach the students.</p>
                    </div>
                    <h1>NOW YOUR TUTOR IS JUST ONE STEP AWAY</h1>
                    <h4>LET'S GAIN TOGETHER</h4>
                    </div>
                </div>
                <div className="col-md-4">
                    <div className="container card">
                    <div className="top">
                        <h2 className="name">Ashutosh Kumar</h2>
                        <img className="circle-img" src="https://461200-1444427-raikfcquaxqncofqfm.stackpathdns.com/wp-content/uploads/2021/01/graphic-design.jpg" alt="avatar_img" />
                    </div>
                    <div className="bottom">
                        <p className="info">Online</p>
                        <p className="info">BCE, Sabour, Bhagalpur</p>
                        <p className="info">Mathematics, English</p>
                        <p className="info">10AM - 12PM</p>
                        <p className="info">₹2000-4000</p>
                        {Ratings(3.4)}
                        <div className="row">
                            <div className="col-6">
                            <button typename="button" className="btn btn-warning">Send Request</button>
                            </div>
                            <div className="col-6">
                            <button typename="button" className="btn btn-danger">Demo Video</button>
                            </div>
                        </div>
                    </div>
                    </div>
                </div>
            </div>
            <div className="container part2">
                <div className="row">
                    <div className="col-5">
                    <div className="input-group mb-3">
                        <input typename="text" className="form-control" placeholder="Search Your Tutor" aria-label="Recipient's username" aria-describedby="button-addon2"/>
                        <button className="btn btn-outline-secondary" type="button" id="button-addon2">Search</button>
                    </div>
                    </div>
                    <div className="col-2">
                        OR
                    </div>
                    <div className="col-5">
                    <button typename="button" className="btn" id="second">Find Me a Tutor</button>
                    </div>
                </div>
            </div>
            <div className="content-3">
                <div className="row">
                    <div className="col-md-6 left">
                        <h6>Find your Tutor today! Download App Now</h6>
                        <div className="download-icons">
                            <button className="btn google" >
                                <span className="google-play-store"></span>
                            </button>
                            <button className="btn apple" >
                                <span className="apple-app-store"></span>
                            </button>
                        </div>
                    </div>
                    <div className="col-md-6 right">
                    <h6>Get Link to download the App</h6>
                    <div className="input-group mb-3">
                        <span className="input-group-text">+91</span>
                        <input typename="number" class="form-control" placeholder="Enter Phone Number here" aria-label="Example text with two button addons"/>
                        <button className="btn btn-danger" type="button">Get Link</button>
                    </div>
                    </div>
                </div>
            </div>
            <div className="lastpart">
                <div className="row">
                    <div className="col-md-7">
                        <h2>Keep Learning along with<br/> free coding classes</h2>
                    </div>
                    <div className="col-md-5">
                        <div className="container">
                            <h6>Coding Classes for age<br/>6-12</h6>
                            <img alt="img" 
                            src="https://thumbs.dreamstime.com/b/programming-code-learning-%C3%A2%E2%82%AC-learn-to-code-text-colored-letters-abstract-technology-background-programming-code-learning-109305083.jpg"
                            />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Content;